.elements {
    margin: 50px auto 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(282px, -webkit-max-content));
    grid-template-columns: repeat(auto-fit, minmax(282px, max-content));
    grid-gap: 20px 17px;
    gap: 20px 17px;
    max-width: 880px;
    justify-content: center;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
    .elements {
        margin-top: 50px;
        grid-gap: 20px 17px;
        gap: 20px 17px;
        width: 584px;
    }
}

